import React, { Component } from 'react';
import $ from 'jquery';

class RecentlyPlayed extends Component{
    constructor(){
        super();
        this.state = {
            recents: null
            }
    }
    componentDidMount(){
        this.getPrevData();
        this.getEachPrev();
    }

    getPrevData(){
        $.ajax({
            url: "https://api.spotify.com/v1/me/player/recently-played",
            type: "GET",
            beforeSend: (xhr) => {
                xhr.setRequestHeader("Authorization", "Bearer " + this.props.token);
            },
            success: (data) => {
                this.setState({
                    recents: data.items
                })
                console.log(data);
            }
        })
    }
    getEachPrev(){
        const prevPlayed = this.state.recents ? (
            this.state.recents.map(prev => {
                return(
                    <div>{prev.track.name}</div>
                )
            })
        ) : (
            
            <div> No Recent Replays </div>
            
        )

        return prevPlayed;
    }
    render(){
        return(
            <div>
                <h1>Recently Played</h1>
                <b>{this.getEachPrev()}</b>
            </div>
        )
    }
}

export default RecentlyPlayed;